
import Cropper from 'cropperjs'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import uploadFile from '@/api/uploadFile'
import { dataURLtoFile } from '@/assets/js/utils'

export default {
props: {

headerImage: {
type: String,
default: ''
}
},
data () {
return {
picValue: '',
cropper: '',
croppable: false,
panel: false,
url: '',
postHeaderImg: '',
heightAll: 0
}
},
mounted () {

var self = this
var image = document.getElementById('image')
this.cropper = new Cropper(image, {
aspectRatio: 1,
viewMode: 1,
background: false,
zoomable: false,
ready: function () {
self.croppable = true
}
})
},
components: {
Cell,
CellBox
},
methods: {
publishDynamic () {
this.$refs.selectImg.dispatchEvent(new MouseEvent('click'))
},
getObjectURL (file) {
var url = null
if (window.createObjectURL !== undefined) { // basic
url = window.createObjectURL(file)
} else if (window.URL !== undefined) { // mozilla(firefox)
url = window.URL.createObjectURL(file)
} else if (window.webkitURL !== undefined) { // webkit or chrome
url = window.webkitURL.createObjectURL(file)
}
return url
},
change (e) {
let files = e.target.files || e.dataTransfer.files
if (!files.length) return
if (files[0].size === 0) return
this.panel = true
this.picValue = files[0]
this.url = this.getObjectURL(this.picValue)

if (this.cropper) {
this.cropper.replace(this.url)
}
this.panel = true
},
crop () {
this.panel = false
var croppedCanvas
var roundedCanvas
if (!this.croppable) {
return
}

croppedCanvas = this.cropper.getCroppedCanvas()

roundedCanvas = this.getRoundedCanvas(croppedCanvas)
this.postHeaderImg = roundedCanvas.toDataURL()

this.$emit('getHeaderImage', roundedCanvas.toDataURL())

this.postImg(dataURLtoFile(roundedCanvas.toDataURL('image/png')))
},
getRoundedCanvas (sourceCanvas) {
var canvas = document.createElement('canvas')
var context = canvas.getContext('2d')
var width = sourceCanvas.width
var height = sourceCanvas.height

if (width > 100) {
width = 100
}
if (height > 100) {
height = 100
}
canvas.width = width
canvas.height = height
context.imageSmoothingEnabled = true
context.drawImage(sourceCanvas, 0, 0, width, height)
context.globalCompositeOperation = 'destination-in'
context.beginPath()
context.arc(width / 2, height / 2, Math.min(width, height) / 2, 0, 2 * Math.PI, true)
context.fill()
return canvas
},
postImg (file) {
uploadFile(this, [file], (data) => {
this.$emit('getHeader', data[0].downloadPath)
})
}
}
}



