
import VHeader from '@/components/v-header/v-header'
import imageTop from '@/assets/img/default_avatar.png'
import { isIOS, isIphoneX } from '@/assets/js/brower'
import Cropper from '@/components/order/cropper'
import Scroller from 'vux/src/components/scroller/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { localUser } from '@/assets/js/local'
import axios from '@/api/axiosApi'

export default {
data () {
return {
imageTop: imageTop,
styles: {
height: '0px'
},
user: {
nickname: '',
realName: '',
phoneNum: '',
email: '',
birth: '',
signature: ''
},
downloadPath: '',
showTextArea: false,
signatureCopy: '',
headerImage: ''
}
},
methods: {
publishDynamic () {
this.$refs.selectImg.dispatchEvent(new MouseEvent('click'))
},
handleSubmitUser () {
if (this.user.nickname === '') {
this.$vux.toast.text('', 'middle')
return
}
this.user.headUrl = this.user.uploadImg
axios.put(this, '/v1/userInfo/' + this.user.id, {nickName: this.user.nickname, realName: this.user.nickname, email: this.user.email, birth: this.user.birth, signature: this.user.signature}, (data) => {
localUser.set(this.user)
this.$vux.toast.text('', 'middle')
/*
const JIM = this.$store.getters.JIM
if (JIM) {
JIM.updateSelfInfo({
nickname: this.user.nickname
})
}
*/
})
},
textAreaContent () {
if (isIOS()) {
this.showTextArea = false
} else {
this.showTextArea = true
}
},
onCancel () {
},
onConfirm () {
const t = this.signatureCopy
this.user.signature = t
},
newHeaderImage (newImg) {
this.user.uploadImg = newImg
localUser.setItem('uploadImg', newImg)
this.$vux.toast.text('', 'middle')
axios.put(this, '/v1/userInfo/' + this.user.id, {headUrl: newImg}, (data) => {
})
}
},
created () {
let obj = localUser.get()
if (obj == null) {
obj = {}
} else {
if (obj.uploadImg == null) {
obj.uploadImg = ''
}
if (obj.signature == null || typeof obj.signature === 'undefined') {
obj.signature = ''
}
}
this.user = obj
this.signatureCopy = obj.signature
if (this.user.nickname === '' || this.user.nickname == null) {
this.user.nickname = '' + this.user.phoneNum
}
},
mounted () {
this.styles.height = window.innerHeight - 64 + 'px'
},
components: {
VHeader,
Scroller,
Cell,
CellBox,
imageTop,
XInput,
Calendar,
XTextarea,
XButton,
Group,
Datetime,
Confirm,
isIOS,
isIphoneX,
Cropper,
Flexbox,
FlexboxItem
}
}



